<template>
  <div class="count_box">
    <button @click="handleSub" class="minus">-</button>
    <input class="inp" @change="handleChange" type="text" :value="value">
    <button @click="handleAdd" class="add">+</button>
  </div>
</template>

<script>
export default {
  name: 'CountBox',
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  data () {
    return {

    }
  },
  methods: {
    handleAdd () {
      if (this.value >= 999) return
      this.$emit('input', this.value + 1)
    },

    handleSub () {
      if (this.value <= 1) return
      this.$emit('input', this.value - 1)
    },

    handleChange (e) {
      const num = +e.target.value
      if (isNaN(num) || num < 1) {
        // 回退
        e.target.value = this.value
        return
      }

      this.$emit('input', num)
    }

  }
}
</script>

<style lang="less" scoped>
.count_box{
  display: flex;
  .inp{
    height: 30px;
    width: 40px;
    margin: 0 5px;
    outline: none;
    border:none;
    background-color: #efefef;
    text-align: center;
  }
  .add, .minus{
    height: 30px;
    width: 30px;
    outline: none;
    border:none;
    background-color: #efefef;

  }
}
</style>
